import React from "react";
import {makeStyles} from "@material-ui/core/styles";
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import Buttons from "./Buttons";

const useStyles = makeStyles({
	desc: {
		margin: "0",

		lineHeight: "130%",
		marginBottom: "20px"
	},

	group: {
		width: "100%",

		"& label": {
			display: "flex",
			alignItems: "flex-start",
			margin: "0",
			marginBottom: "20px",
			marginLeft: "-9px"
		},

		"& img": {
			width: "auto",
			height: "auto",
			maxWidth: "100%",
			border: "3px solid transparent",
			transition: ".2s ease-in-out"
		},

		"& .Mui-checked +.MuiTypography-root img": {
			border: "3px solid #ffa0af",
		}

	},

	groupTitle: {
		marginTop: "10px",
		marginBottom: "0",
		fontWeight: "700",
		fontSize: "14px"
	},

	groupDesc: {
		margin: "10px 0 15px 0",
		fontSize: "14px"
	},


})

const Item = (props) => {
	const classes = useStyles();

	const currentItems = () => {
		const data = props.data.find(item => item.name === props.condition)

		data.items = data.items.filter(item => item.quantity > 0)

		return data;
	}
	const resultItem = props.data.find(item => item.name === "result")

	const handleChange = (event) => {
		props.handleChange(event.target.value)
	};

	return (
		<div>
			<p className={classes.desc} dangerouslySetInnerHTML={{__html: currentItems().desc}}></p>
			<FormControl component="fieldset">
				<RadioGroup aria-label={props.condition}
							name={props.condition}
							className={classes.group}
							value={props.value}
							onChange={handleChange}>
					{currentItems().items.map((item, i) => {
						return <FormControlLabel key={i}
												 value={item.code}
												 control={<Radio />}
												 label={
							<div>
								<p className={classes.groupTitle}>
									{item.title}
								</p>
								<p className={classes.groupDesc}>
									{props.condition === "base"
										? item.type === "standard"
											? <span dangerouslySetInnerHTML={{__html: "- –ø–µ–ª—ë–Ω–∫–∞ 90*120 —Å–º,</br>- –Ω–∞–≥—Ä—É–¥–Ω–∏–∫ (> 6 –º–µ—Å),</br>- –≥—Ä—ã–∑—É–Ω–æ–∫ –∏–∑ –±—É–∫–∞ —Å —à—É—Ä—à–∞—â–∏–º–∏ —É—à–∫–∞–º–∏,</br>* –º–∞—Ç–µ—Ä–∏–∞–ª: –º—É—Å–ª–∏–Ω (100% —Ö–ª–æ–ø–æ–∫)"}}></span>
											: item.option === 1
												? <span dangerouslySetInnerHTML={{__html: "- –æ–¥–µ—è–ª–æ 90*120 —Å–º,<br>- –Ω–∞–≥—Ä—É–¥–Ω–∏–∫ (> 6 –º–µ—Å),<br>- –≥—Ä—ã–∑—É–Ω–æ–∫ –∏–∑ –±—É–∫–∞ —Å —à—É—Ä—à–∞—â–∏–º–∏ —É—à–∫–∞–º–∏,</br>* –º–∞—Ç–µ—Ä–∏–∞–ª: –º—É—Å–ª–∏–Ω (100% —Ö–ª–æ–ø–æ–∫)"}}></span>
												: <span dangerouslySetInnerHTML={{__html: "- –ø–µ–ª—ë–Ω–∫–∞ 90*120 —Å–º,<br>- —à–∞–ø–æ—á–∫–∞ (0-3 –º–µ—Å),<br>- –Ω–∞–≥—Ä—É–¥–Ω–∏–∫ (> 6 –º–µ—Å),<br>- –≥—Ä—ã–∑—É–Ω–æ–∫ –∏–∑ –±—É–∫–∞ —Å —à—É—Ä—à–∞—â–∏–º–∏ —É—à–∫–∞–º–∏,</br>* –º–∞—Ç–µ—Ä–∏–∞–ª: —Ç—Ä–∏–∫–æ—Ç–∞–∂ (95% —Ö–ª–æ–ø–æ–∫, 5% –ª–∞–π–∫—Ä–∞)"}}></span>
										: ''
									}
								</p>
								{item.img
									? <img className={classes.groupImg}
										   src={require(`../img/${props.condition}/${item.img}`).default}/>
									: ''
								}
							</div>}/>

					})

					}

				</RadioGroup>
			</FormControl>
			{props.condition === "base" && <p>*–î–ª—è –ø—Ä–æ–¥–æ–ª–∂–µ–Ω–∏—è –≤—ã–±–µ—Ä–∏—Ç–µ –ø–æ–∑–∏—Ü–∏—é</p>}
			<Buttons disable={props.value !== '' ? 1 : 0}
					 condition={props.condition}
					 handleCondition={props.handleCondition}/>
		</div>

	);
}

export default Item;
