import React from "react";
import {makeStyles} from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Item from "./Item";
import Result from "./Result";
import Intro from "./Intro";

const useStyles = makeStyles({

	subtitle: {
		fontWeight: "400",
		fontSize: "16px",
		margin: "15px 0",
		letterSpacing: "0.6px",
		textAlign: "center",

		"& span": {
			position: "relative",
			borderBottom: "1px solid #ffa0af"
		}


	},

})

const MainBlock = (props) => {
	const classes = useStyles();

	return (
		<div>
			<Typography component="h2" className={classes.subtitle}>
				{props.condition === "intro"
					? <span>–ß—Ç–æ —Ç–∞–∫–æ–µ Raspberry Box?</span>
					:props.condition === "base"
						? <span>–í—ã–±–µ—Ä–∏—Ç–µ –±–∞–∑–æ–≤—ã–π –∫–æ–º–ø–ª–µ–∫—Ç:</span>
						: props.condition === "toy"
							? <span>–í—ã–±–µ—Ä–∏—Ç–µ –∏–≥—Ä—É—à–∫—É:</span>
							: props.condition === "accessory"
								? <span>–ó–∞–º–µ–Ω–∏—Ç—å –≥—Ä—ã–∑—É–Ω–æ–∫-—É—à–∫–∏ –Ω–∞ –≥—Ä—ã–∑—É–Ω–æ–∫ —Ä—É—á–Ω–æ–π –≤—è–∑–∫–∏?</span>
								: props.condition === "extra"
									? <span>–î–æ–±–∞–≤–∏—Ç—å –º–æ—Ä–æ–∂–µ–Ω–æ–µ?</span>
									:  props.condition === "postcard"
										? <span>–í—ã–±–µ—Ä–∏—Ç–µ –æ—Ç–∫—Ä—ã—Ç–∫—É:</span>
										: ''
				}
			</Typography>
			{props.condition === "result"
				? <Result data={props.data}
						  code={props.code}
						  condition={props.condition}
						  handleResult={props.handleResult}
						  handleCondition={props.handleCondition}
						  handleLink={props.handleLink}/>
				: props.condition === "intro"
					? <Intro data={props.data}
							 value={props.value}
							 condition={props.condition}
							 handleChange={props.handleChange}
							 handleCondition={props.handleCondition}/>
					: <Item data={props.data}
						  value={props.value}
						  condition={props.condition}
						  handleChange={props.handleChange}
						  handleCondition={props.handleCondition}/>
			}
		</div>

	);
}

export default MainBlock;
