import React from "react";
import {makeStyles} from "@material-ui/core/styles";
import ButtonDone from "./ButtonDone";

const useStyles = makeStyles({
	desc: {
		fontWeight: "400",
		fontSize: "16px",
		marginBottom: "20px",
		letterSpacing: "0.6px",
		textAlign: "center",
		lineHeight: "130%"
	},
	
	code: {
		backgroundColor: "#ffa0af",
		color: "#ffffff",
		fontWeight: "700",
		letterSpacing: "0.1em",
		textAlign: "center",
		padding: "20px 15px",
		lineHeight: "140%",
		fontSize: "16px",
		marginBottom: "20px"
	},
	contain: {
		margin: "40px 0 20px 0",
		fontWeight: "700"
	},

	list: {
		lineHeight: "135%",
		paddingLeft: "15px",
		marginBottom: "25px",

		"& li": {
			marginBottom: "15px"
		}
	},

	descript: {
		paddingLeft: "15px"
	},

	groupImg: {
		maxWidth: "100%"
	}

})

const Result = (props) => {
	const classes = useStyles();

	const resultArr = () => {
		const codeInit = props.code.split("-").slice(0,-1);
		const dataInit = props.data

		let newArr = codeInit.map((item, i) => {

			const current = dataInit[i].items.find(elem => {
				if (elem.code === item && elem.img) {
					return elem;
				}
			})

			if (current) {
				item = {}
				item.title = current.title;
				item.img = current.img;
				item.condition = dataInit[i].name;

				if (dataInit[i].name === "base") {
					if (current.type === "standard") {
						item.desc = "- –ø–µ–ª—ë–Ω–∫–∞ 90*120 —Å–º,</br>- –Ω–∞–≥—Ä—É–¥–Ω–∏–∫ (> 6 –º–µ—Å),</br>- –≥—Ä—ã–∑—É–Ω–æ–∫ –∏–∑ –±—É–∫–∞ —Å —à—É—Ä—à–∞—â–∏–º–∏ —É—à–∫–∞–º–∏ (–∏–ª–∏ –∑–∞–º–µ–Ω–∞ –Ω–∞ –≤—è–∑–∞–Ω—ã–π),</br>* –º–∞—Ç–µ—Ä–∏–∞–ª: –º—É—Å–ª–∏–Ω (100% —Ö–ª–æ–ø–æ–∫)"
					} else if (current.option === 1) {
						item.desc = "- –æ–¥–µ—è–ª–æ 90*120 —Å–º,<br>- –Ω–∞–≥—Ä—É–¥–Ω–∏–∫ (> 6 –º–µ—Å),<br>- –≥—Ä—ã–∑—É–Ω–æ–∫ –∏–∑ –±—É–∫–∞ —Å —à—É—Ä—à–∞—â–∏–º–∏ —É—à–∫–∞–º–∏ (–∏–ª–∏ –∑–∞–º–µ–Ω–∞ –Ω–∞ –≤—è–∑–∞–Ω—ã–π),</br>* –º–∞—Ç–µ—Ä–∏–∞–ª: –º—É—Å–ª–∏–Ω (100% —Ö–ª–æ–ø–æ–∫)"
					} else {
						item.desc = "- –ø–µ–ª—ë–Ω–∫–∞ 90*120 —Å–º,<br>- —à–∞–ø–æ—á–∫–∞ (0-3 –º–µ—Å),<br>- –Ω–∞–≥—Ä—É–¥–Ω–∏–∫ (> 6 –º–µ—Å),<br>- –≥—Ä—ã–∑—É–Ω–æ–∫ –∏–∑ –±—É–∫–∞ —Å —à—É—Ä—à–∞—â–∏–º–∏ —É—à–∫–∞–º–∏ (–∏–ª–∏ –∑–∞–º–µ–Ω–∞ –Ω–∞ –≤—è–∑–∞–Ω—ã–π),</br>* –º–∞—Ç–µ—Ä–∏–∞–ª: —Ç—Ä–∏–∫–æ—Ç–∞–∂ (95% —Ö–ª–æ–ø–æ–∫, 5% –ª–∞–π–∫—Ä–∞)"
					}
				}


			}

			return item;

		})

		newArr = newArr.filter(item => typeof item !== 'string')
		return newArr;
	}

	return (
		<div className={classes.result}>
			<p className={classes.desc}>–î–ª—è –∑–∞–∫–∞–∑–∞ –æ—Ç–ø—Ä–∞–≤—å—Ç–µ —Å—Ñ–æ—Ä–º–∏—Ä–æ–≤–∞–Ω–Ω—ã–π –∫–æ–¥ –≤ –ª–∏—á–Ω—ã–µ —Å–æ–±—â–µ–Ω–∏—è @raspberry__leaf –∏–ª–∏ –Ω–∞ WhatsApp</p>
			<p className={classes.code}>{props.code}</p>
			<ButtonDone condition={props.condition}
						descr={"insta"}
						direction={"none"}
						handleLink={props.handleLink}
						code={props.code}
			/>
			<ButtonDone condition={props.condition}
						descr={"whats"}
						direction={"none"}
						handleLink={props.handleLink}
						code={props.code}
			/>
			<div className={classes.contain}>
				–°–æ—Å—Ç–∞–≤ ¬´Raspberry Box¬ª:
			</div>
			<ol className={classes.list}>
				{resultArr().map((item, i) => {
					return <li key={i}>
						<p className={classes.title}>{item.title}</p>
						{item.desc
							? <p className={classes.descrip} dangerouslySetInnerHTML={{__html: `${item.desc}`}}></p>
							: ''
						}
						<img className={classes.groupImg} src={require(`../img/${item.condition}/${item.img}`).default}></img>
					</li>
				})}
					<li>–≠–∫–æ-—Å—É–º–∫–∞ —Ä—É—á–Ω–æ–π —Ä–∞–±–æ—Ç—ã</li>
					<li>–ë–µ—Å–ø–ª–∞—Ç–Ω–∞—è –¥–æ—Å—Ç–∞–≤–∫–∞ –ø–æ –†–æ—Å—Å–∏–∏</li>

			</ol>
			<ButtonDone condition={props.condition}
						direction={"prev"}
						handleCondition={props.handleCondition}/>
		</div>

	);
}

export default Result;
